import { useLocation } from 'react-router-dom'
import { asyncRouter } from '@/pages/index'
import { useMemo } from 'react'

const useBreadcrumb = () => {
  let { pathname } = useLocation()//现在的路由
  let content = [...asyncRouter]//存放路由数据
  let data = [content[0]]
  return useMemo(() => {
    content.forEach(ele1 => {
      if (ele1.children) {
        ele1.children.forEach(ele2 => {
          if (ele2.path == pathname) {
            data.push(ele1)
            data.push(ele2)
          }
        })
      } else {
        if (ele1.path == pathname) {
          data.push(ele1)
        }
      }
    })
    return data
  }, [pathname])
}
export default useBreadcrumb