
import { Layout } from 'antd';
import {Outlet} from 'react-router-dom'
const {  Content } = Layout;
function MainContent() {
  return (
    <Content
      className="site-layout-background"
      style={{
          margin: '24px 16px',
          padding: 24,
        minHeight: 280,
      }}
    >
      <Outlet></Outlet>
    </Content>
  )
}
export default MainContent