import {
  MenuUnfoldOutlined,
  MenuFoldOutlined,
  FullscreenOutlined,
  FullscreenExitOutlined,
  FontColorsOutlined,
  FontSizeOutlined,
} from '@ant-design/icons';
import MyBreadcrumb from '../component/breadcrumb';
import screenfull from 'screenfull';
import { useState,useEffect } from 'react';
// <FullscreenOutlined />
// <FullscreenExitOutlined />
//<FontSizeOutlined />
//<FontColorsOutlined />
const toggleFull = () => {
  if (screenfull.isEnabled) {
    screenfull.toggle()
  }
}
const Toggle = ({ value, onChange }) => {
  const [full, setFull] = useState(false)
  useEffect(() => {
    screenfull.onchange(() => {
      setFull(screenfull.isFullscreen)
    })
  }, [])
  console.log(full);
  return (
    <div
      className="site-layout-background"
      style={{ padding: 0, backgroundColor: '#fff' }}
    >
      <div className="trigger" onClick={onChange}>
        {value ? <MenuUnfoldOutlined /> : <MenuFoldOutlined />}
      </div>
      <MyBreadcrumb></MyBreadcrumb>
      <div className="box">
        {full ? (
          <FullscreenOutlined onClick={toggleFull} />
        ) : (
          <FullscreenExitOutlined onClick={toggleFull} />
        )}
        <FontColorsOutlined />
        <FontSizeOutlined />
      </div>
    </div>
  );
};
function MyHeader(props) {
  return <Toggle {...props}></Toggle>;
}
export default MyHeader;
