import { Route, HashRouter, Routes } from 'react-router-dom';
//*引入组件
import LayoutApp from '@/pages/layout/index';
import Login from '@/pages/login/login';
import { asyncRouter } from '@/pages/index';
function CreateItem(asyncRouter) {
  // console.log(asyncRouter);
  let Item = [];
  asyncRouter.forEach((item) => {
    if (item.path && item.element) {
      Item.push(
        <Route key={item.key} path={item.path} element={item.element}></Route>
      );
    }
    if (item.children) {
      item.children.forEach((ele) => {
        Item.push(
          <Route key={ele.key} path={ele.path} element={ele.element}></Route>
        );
      });
    }
  });
  //   console.log(Item);
  return Item;
}
function App() {
  return (
    <HashRouter>
      {/* <Link to="/">首页</Link>
            <Link to="/login">登录</Link> */}
      {/* to="/" 跳转路径 */}
      <Routes>
        {/* 配置规则 */}
        <Route path="/" element={<LayoutApp />}>
          {CreateItem(asyncRouter)}
        </Route>
        {/* path：路径  element：要渲染的组件*/}
        <Route path="/login" element={<Login />}></Route>
      </Routes>
    </HashRouter>
  );
}

export default App;
