import { MenuFoldOutlined, MenuUnfoldOutlined } from '@ant-design/icons';
import { Button, Menu } from 'antd';
import React, { useState } from 'react';
import logo1 from '@/assets/img/logo.webp';
import logo2 from '@/assets/img/logo2.jpg';
import { asyncRouter } from '@/pages/index';
import { Link } from 'react-router-dom';
import useMenu from '../hooks/useMenu';
// import useBreadcrumb from '../hooks/useBreadcrumb';

//logo
const Logo = ({ value }) => {
  return (
    <div className={`logo ${value ? 'small' : ''}`}>
      <img src={value ? logo2 : logo1} alt="" />
    </div>
  );
};
function getItem(label, key, icon, children, path) {
  return {
    key,
    icon,
    children,
    label: path ? <Link to={path}>{label}</Link> : label,
  };
}
function CreateItem(asyncRouter) {
  // console.log(asyncRouter);
  let Item = [];
  asyncRouter.forEach((item) => {
    if (!item.hidden) {
      if (!item.children) {
        Item.push(getItem(item.label, item.key, item.icon, null, item.path));
      } else {
        Item.push(
          getItem(
            item.label,
            item.key,
            item.icon,
            CreateItem(item.children),
            item.path
          )
        );
      }
    }
  });
  // console.log(Item);
  return Item;
}
function Sidebar(props) {
  const [selectedKey, openKey] = useMenu();
  // let a= useBreadcrumb()
  // console.log(a);
  return (
    // < trigger={null} collapsible collapsed={collapsed}>
    <>
      <Logo {...props}></Logo>

      <Menu
        theme="dark"
        mode="inline"
        defaultSelectedKeys={selectedKey} /* 菜单高亮 */
        defaultOpenKeys={openKey} /* 二级菜单选中 */
        items={CreateItem(asyncRouter)}
      />
    </>
  );
}
export default Sidebar;
