import { Button,Tooltip } from 'antd';
import { SearchOutlined } from '@ant-design/icons';
import React from 'react';
function Login() {
  return (
    <div>
      <h2>登录组件</h2>
       <Tooltip title="search">
      <Button type="primary" shape="circle" icon={<SearchOutlined />} />
    </Tooltip>
    </div>
  )
}
export default Login